import React from "react";
import CreateIcon from "@material-ui/icons/Create";
import HelpOutlineIcon from "@material-ui/icons/HelpOutline";
import StorageIcon from "@material-ui/icons/Storage";
import { withRouter } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import styled from "styled-components";

const StyledAside = styled.aside`
  text-align: center;
  .leftAside {
    margin-top: -700px;
    margin-left: 270px;
    width: 100px;
  }
  .rightAside {
    margin-top: -100px;
    margin-left: 1650px;
    width: 100px;
  }
`;
const useStyles = makeStyles(() => ({
  asideIcon: {
    fontSize: "3rem",
    cursor: "pointer",
  },
}));
const AsideView = ({ history }) => {
  const classes = useStyles();

  const goWriteRequest = () => {
    history.push("/writeRequest");
  };

  const goWiki = () => {
    document.location.href = "http://wiki.payday.co.kr:12000/payday/";
  };

  const goRequestList = () => {
    document.location.href = "http://rnd.himgt.net:8080/request/request";
  };
  return (
    <StyledAside>
      <div className="leftAside">
        <CreateIcon onClick={goWriteRequest} className={classes.asideIcon} />
        <br />
        <br />
        <HelpOutlineIcon onClick={goWiki} className={classes.asideIcon} />
      </div>
      <div className="rightAside">
        <StorageIcon onClick={goRequestList} className={classes.asideIcon} />
      </div>
    </StyledAside>
  );
};

export default withRouter(AsideView);
